import './vendor';
require ('./vendor/jquery-migrate-1.2.1.min.js');
require ('./vendor/slick.min.js');
require ('./vendor/jquery.validate.min.js');
require ('./vendor/jquery.mask.min.js');
require ('./vendor/mapparams.js');
require ('./vendor/jquery.mCustomScrollbar.concat.min.js');

$(document).ready(function () {
   setTimeout(function () {
    var preloader = $('#page-preloader');
    if (!preloader.hasClass('done')) {
        preloader.addClass('done');
    }
   }, 1000);
});

var menu_selector = "#bottom-menu"; // Переменная должна содержать название класса или идентификатора, обертки нашего меню.
$(document).ready(function () {
    // hover header
    hoverHeader();
    // slick slider
    sliderSlick();
    // hous tabs
    housTab();
    // hous popup
    housPop();
    // popup
    popup();
    // accordion ipoteka
    accordionIpoteka();
    // валидация
    valid('#form');
    valid('#form-2');
    // scroll Top arrow and nav-menu
    topScroll();
    // fullscreen maps
    mapFullScreen();
    // mask
    $('.form__phone').mask('+7 (999) 999-99-99');
    // map
    clickTabMaps();
    // burger menu
    burger();
    // custom Scroll
    myScrollCustom();
    // smooth anchorage
    $(document).on("scroll", onScroll);
    $(menu_selector).on("click","a", function(e){
        e.preventDefault();
        $(document).off("scroll");
        $(menu_selector + " a.active").removeClass("active");
        $(this).addClass("active");
        var hash = $(this).attr("href");
        var target = $(hash);
        var top = target.offset().top;
        $("html, body").animate({
            scrollTop: top
        }, 1000, function(){
            window.location.hash = hash;
            $(document).on("scroll", onScroll);
        });
    });
    // genplan
   // genPlan();
    planPopup();
});

// hover эффект для bg header
function hoverHeader() {
    var $headerBtn = $('.header__box .btn'),
        $headerWrap = $('.header__wrap');
    $headerBtn.hover
    (function () {
            $headerWrap.addClass('header__wrap--skale');
            if (navigator.userAgent.indexOf("Firefox") > 0) {  // отдельный hover для браузера firefox
                $headerWrap.removeClass('header__wrap--skale');
                $headerWrap.addClass('header--firefox');
            }
        },
        function () {
            $headerWrap.removeClass('header__wrap--skale');
            if (navigator.userAgent.indexOf("Firefox") > 0) {
                $headerWrap.removeClass('header--firefox');
            }
        });
}

// slick slider
function sliderSlick() {
    var $status = $('.slider__number'),
        $slickElement = $('.slider'),
        $popFor = $('.pop__for'),
        $popNav = $('.pop__nav');

    // находим какой слайдер выбран и количество слайдеров вообщем
    $slickElement.on('init reInit afterChange', function (event, slick, currentSlide, nextSlide) {
        //currentSlide is undefined on init -- set it to 0 in this case (currentSlide is 0 based)
        var i = (currentSlide ? currentSlide : 0) + 1;
        $status.text(i + '/' + slick.slideCount);
    });

    // слайдер для блока соседей
    $slickElement.slick({
        arrows: true,
        dots: true
    });

    // слайдер для вспывающего окна в блоке hous
    $popFor.slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        fade: true,
        asNavFor: '.pop__nav'
    });
    $popNav.slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        arrows: false,
        asNavFor: '.pop__for',
        centerMode: true,
        focusOnSelect: true
    });
}

// табы для переключение между вариантов домов в блоке hous
function housTab() {
    var $tabsOne = $('#hous__tabs-one'),
        $tabsTwo = $('#hous__tabs-two'),
        $tabOne  = $('#hous__tab-one'),
        $tabTwo  = $('#hous__tab-two'),
        tabsActive = 'hous__tabs--active',
        tabActive  = 'hous__tab--active';
    $tabsOne.on('click', function (e) {
        e.preventDefault();
        $(this).addClass(tabsActive);
        $tabsTwo.removeClass(tabsActive);
        $tabOne.addClass(tabActive);
        $tabTwo.removeClass(tabActive);
    });
    $tabsTwo.on('click', function (e) {
        e.preventDefault();
        $(this).addClass(tabsActive);
        $tabsOne.removeClass(tabsActive);
        $tabTwo.addClass(tabActive);
        $tabOne.removeClass(tabActive);
    });
}
// popup для блока hous
function housPop() {
    $('.hous__item').on('click', function () {
        $(this).next('.pop').addClass('pop--active');
    });
    $('.pop__close').on('click', function () {
        $(this).closest('.pop').removeClass('pop--active');
    });
}
//popup для кнопок заказать звонок
function popup() {
    $('.btn-popup').on('click', function (e) {
        e.preventDefault();
        $('.popup').addClass('popup--active');
        $('#scrollBar').addClass('overflow-y-hidden');
    });
    $('.popup__close').on('click', function (e) {
        e.preventDefault();
        $('.popup').removeClass('popup--active');
        $('#scrollBar').removeClass('overflow-y-hidden');
    });
}
// accordion для блока ipoteka
function accordionIpoteka() {
    var $ipotekaSub = $('.ipoteka__subtitle'),
        $ipotekaTxt = $('.ipoteka__text');
    $ipotekaSub.on('click', function () {
        var $this = $(this);
        if (!$this.hasClass('ipoteka__subtitle--active')) {
            $ipotekaTxt.slideUp();
            $ipotekaSub.removeClass('ipoteka__subtitle--active');
        }
        $(this).next('.ipoteka__text').slideToggle();
        $(this).toggleClass('ipoteka__subtitle--active');
    });
}
// валидаци форм
function valid(form) {
    $.validator.addMethod("minlenghtphone", function (value, element) {

            return value.replace(/\D+/g, '').length > 10;
        },
    "");
    $.validator.addMethod("requiredphone", function (value, element) {

            return value.replace(/\D+/g, '').length > 1;
        },
    "");
    $(form).validate({
        rules: {
            name: {
                required: true,
                minlength: 2
            },
            phone: {
                 requiredphone: true,
                 minlenghtphone: true
            },
            terms: {
                required: true
            }
        },
        messages: {
            name: {
                required: ''
            },
            phone: {
                required: ''
            },
            terms: {
                required: ''
            }
        },
        errorPlacement: function(error, element) {},
        highlight: function(element, errorClass, validClass) {
            $(element).prev(".form__text").addClass(errorClass).removeClass(validClass);
            $(element).next(".checkbox__text").addClass(errorClass).removeClass(validClass);
            $(element).addClass(errorClass).removeClass(validClass);
        },
        unhighlight: function(element, errorClass, validClass) {
            $(element).prev(".form__text").removeClass(errorClass).addClass(validClass);
            $(element).next(".checkbox__text").removeClass(errorClass).addClass(validClass);
            $(element).removeClass(errorClass).addClass(validClass);
        },
        submitHandler: function submitHandler(form) {
            //alert("Submitted!");
            $.ajax({
                type: $(form).attr('method'),
                url: $(form).attr('action'),
                data: $(form).serialize(),
                success: function (data) {
                    if(parseInt(data) == 1) {
                        $(form).removeClass('form--active');
                        $(form).siblings('.form__ok').addClass('form--active');
                    } else {
                        $(form).removeClass('form--active');
                        $(form).siblings('.form__ok').addClass('form--active');
                    }
                },
                error: function () {
                    $(form).removeClass('form--active');
                    $(form).siblings('.form__ok').addClass('form--active');
                }
            });

            return false;
        }
    });
}
// scroll Top arrow and nav-menu
function topScroll() {
    var $arrow        = $('.arrow-up'),
        headerTop     = $('.header').height(),
        $burger       = $('.burger'),
        $navMenuFixed = $('.nav-menu--fixed'),
        navMenuHeight = $navMenuFixed.innerHeight();
    $(document).scroll(function () {
        var $scrollTop = $(document).scrollTop();
        if ($scrollTop > headerTop) {
            $arrow.addClass('arrow-up--active');
            $burger.addClass('burger--active');
        } else {
            $arrow.removeClass('arrow-up--active');
            $burger.removeClass('burger--active');
            $burger.removeClass('burger--close');
            $('.bottom-menu').removeClass('bottom-menu--active');
        }
        if ($scrollTop > (headerTop - navMenuHeight)) {
            $navMenuFixed.css('visibility', 'visible');
        } else {
            $navMenuFixed.removeAttr('style');
        }
    });
    $arrow.on('click', function (e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0},1000);
    });
    $('.nav-menu__link').on('click', function (e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0},1000);
    });
}
// fullscreen maps
function mapFullScreen() {
    $('.contact__open').on('click', function (e) {
        e.preventDefault();
       $('.contact__pop').addClass('contact__pop--active');
        $('html, body').addClass('overflow-y-hidden');
    });
    $('.contact__close').on('click', function (e) {
        e.preventDefault();
        $('.contact__pop').removeClass('contact__pop--active');
        $('html, body').removeClass('overflow-y-hidden');
    });
}
function clickTabMaps() {
    $('#contact__tab-one').on('click', function (e) {
        e.preventDefault();
        $('#maps-one').addClass('contact__maps--active');
        $('#maps-two').removeClass('contact__maps--active');
        $('.contact__tab').removeClass('contact__tab--active');
        $(this).addClass('contact__tab--active');
    });
    $('#contact__tab-two').on('click', function (e) {
        e.preventDefault();
        $('#maps-two').addClass('contact__maps--active');
        $('#maps-one').removeClass('contact__maps--active');
        $('.contact__tab').removeClass('contact__tab--active');
        $(this).addClass('contact__tab--active');
    });
}
// burger menu
function burger() {
    $('.burger').on('click', function () {
        $(this).toggleClass('burger--close');
       $('.bottom-menu').toggleClass('bottom-menu--active');
    });
}
// custom Scroll
function myScrollCustom() {
    $(window).on("load",function(){
        $(".pop__text").mCustomScrollbar({
            theme: "rounded-dark"
        });
    });
}
// smooth anchorage
function onScroll(){
    var scroll_top = $(document).scrollTop();
    $(menu_selector + " a").each(function(){
        var hash = $(this).attr("href");
        var target = $(hash);
        if ((target.position().top - 5) <= scroll_top && target.position().top + target.outerHeight() > scroll_top) {
            $(menu_selector + " a.bottom-menu__item").removeClass("bottom-menu__item--active");
            $(this).addClass("bottom-menu__item--active");
        } else {
            $(this).removeClass("bottom-menu__item--active");
        }
    });
}
// genplan
function genPlan() {
    var $areaBtn = svgArea,
        $houseBtn = svgHouse,
        estate = $('#plan__estate span'),
        square = $('#plan__acreage span'),
        street = $('#plan__street span'),
        estBtn;
    $areaBtn.hover(function () {
        if ($(this).is('[id]')) {
            var btnId = $(this).attr('id');
            btnId = btnId.slice(4);
            btnId = Number(btnId);
            estBtn = btnId % 100;
            if(!$(this).hasClass('plan__active')) {
                $areaBtn.removeClass('plan__active');
            }
            $(this).toggleClass('plan__active');
            estate.text('№ ' + estBtn);
            streetText(btnId);
        }
    });
    $houseBtn.hover(function () {
        var houseId = $(this).attr('id');
        houseId = houseId.slice(4);
        var houseNum = Number(houseId);
        estBtn = houseNum % 100;
        var $thisArea = $('#pha-' + houseId + '');
        if (!$thisArea.hasClass('plan__active')) {
            $areaBtn.removeClass('plan__active');
        }
        $thisArea.toggleClass('plan__active');
        estate.text('№ ' + estBtn);
        streetText(houseNum);
    });
    function streetText(streetBtn) {
        if (streetBtn > 0 && streetBtn < 100) {
            street.text('Сосновая');
        }
        if (streetBtn > 100 && streetBtn < 200) {
            street.text('1-я Серебряная');
        }
        if (streetBtn > 200 && streetBtn < 300) {
            street.text('2-я Серебряная');
        }
        if (streetBtn > 300 && streetBtn < 400) {
            street.text('3-я Серебряная');
        }
        if (streetBtn > 400 && streetBtn < 500) {
            street.text('4-я Серебряная');
        }
        if (streetBtn > 500 && streetBtn < 600) {
            street.text('5-я Серебряная');
        }
        if (streetBtn > 600 && streetBtn < 700) {
            street.text('6-я Серебряная');
        }
        if (streetBtn > 700 && streetBtn < 800) {
            street.text('7-я Серебряная');
        }
        if (streetBtn > 800 && streetBtn < 900) {
            street.text('8-я Серебряная');
        }
        if (streetBtn > 900) {
            street.text('Березовая');
        }
    }
}
function planPopup() {
    var btn = $('.plan__item-a');
    var btnClose = $('.plan__popup-close');
    var genplanPopup = $('.plan__popup');
    btn.on('click', function (e) {
        e.preventDefault();
        genplanPopup.addClass('plan__popup--active');
    });
    btnClose.on('click', function (e) {
        e.preventDefault();
        genplanPopup.removeClass('plan__popup--active');
    });
}


